import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-table',
  templateUrl: './app-table.component.html',
  styleUrls: ['./app-table.component.scss']
})

export class AppTableComponent implements OnInit {
  @Input() config_options: ConfigOptions;
  public loading: boolean;
  public data_source: any;
  public no_data: boolean;
  constructor() {
    this.loading = true;
    this.data_source = {};
    this.no_data = false;
  }

  ngOnInit() {
    this.getData()
  }
  getData(): void {
    this.config_options.service[this.config_options.function]().subscribe(res => {
      this.data_source = res;
      this.loading = false;
      this.no_data = res.total_column === 0;
    })
  }

}

class ConfigOptions {
  service: any;
  function: string;
  params: Object
}



